import React, { Fragment } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { observer } from 'mobx-react';
import { Row, Col, Button, Container } from 'react-bootstrap';
import { Formik, Field } from "formik";
import { withCookies } from 'react-cookie';
import Form from 'react-bootstrap/Form';
import './style.css';
import registerForm from '../../../schemes/registerForm';

class Register extends React.Component {
    // Tutorial: https://medium.freecodecamp.org/login-using-react-redux-redux-saga-86b26c8180e

    state = {
        redirect: false,
        firstname: '',
        lastname: '',
        email: '',
        date: '',
        password: '',
        re_password: '',
        errors: []
    };


    componentDidMount() {
    }

    async componentWillReact() {
        if(this.props.store.isAuthenticated) {
            // await this.props.store.fetchProfile()
            this.props.history.push('/')
        }
    }

    changeValue(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    onHandleRegister = (event) => {
        event.preventDefault();
        if(this.state.email === 'admin' && this.state.password === 'admin') {
            this.props.cookies.set('auth', true);
            this.props.store.login();
            this.setState({ redirect: true });
        }
      //  this.props.store.login({ email: this.state.email, password: this.state.password });
    }

    render() {
        const { isAuthenticated } = this.props.store;

        if(this.state.redirect) { //isAuthenticated) {
            return <Redirect to="/login" />;
        }
        else {
            return (
                <Fragment>
                    <Container className="register-container">
                        <Row>
                            <Col className="register-block">
                                <h1>Регистрация</h1>
                                { this.state.errors.length > 0 ?
                                    <div className="errors-block">
                                        <h4>Ошибки</h4>
                                        <ul>
                                            <li>Ошибки</li>
                                        </ul>
                                    </div>
                                    : null
                                    }
                                    <Formik
                                    //инициализируем значения input-ов
                                    initialValues={{
                                        email: "",
                                        username: "",
                                        password: "",
                                        re_password: "",
                                        firstname: "",
                                        lastname: "",
                                        date: ""
                                    }}
                                    //подключаем схему валидации, которую описали выше
                                    validationSchema={registerForm}
                                    //определяем, что будет происходить при вызове onsubmit
                                    onSubmit={values => {
                                        setTimeout(() => {
                                        alert(JSON.stringify(values, null, 2));
                                        }, 500);
                                    }}
                                    //свойство, где описывыем нашу форму
                                    //errors-ошибки валидации формы
                                    //touched-поля формы, которые мы "затронули",
                                    //то есть, в которых что-то ввели
                                    render={({ errors, touched }) => (
                                        <Form className="form-container">
                                        <Form.Row>
                                            <Form.Group as={Col}>
                                                <Form.Label>Имя</Form.Label>
                                                <Field name="firstname" className="form-control" type="text" />
                                            </Form.Group>

                                            <Form.Group as={Col}>
                                                <Form.Label>Фамилия</Form.Label>
                                                <Field name="lastname" className="form-control" type="text" />
                                            </Form.Group>
                                        </Form.Row>

                                    <Form.Group>
                                        <Form.Label>Электронный адрес</Form.Label>
                                        <Field name="email" className="form-control" type="email" placeholder="1234 Main St" />
                                        { errors.email && (<div>{errors.email}</div>)}
                                    </Form.Group>    

                                    <Form.Group>
                                        <Form.Label>Дата рождения</Form.Label>
                                        <Field name="birthday" className="form-control" type="date" placeholder="1234 Main St" />
                                    </Form.Group>

                                    <Form.Group controlId="formGridAddress1">
                                        <Form.Label>Пароль</Form.Label>
                                        <Field name="password" type="password" className="form-control" placeholder="1234 Main St" />
                                        { errors.password && (<div>{errors.password}</div>)}
                                    </Form.Group>

                                    <Form.Group controlId="formGridAddress2">
                                        <Form.Label>Повторите пароль</Form.Label>
                                        <Field name="re_password" type="password" className="form-control" placeholder="Apartment, studio, or floor" />
                                    </Form.Group>
                                    <Form.Group id="formGridCheckbox">
                                        <Form.Check>
                                            <Form.Check.Input type='checkbox' />
                                            <Form.Check.Label>Я согласен с <a href="/rules">правилами</a> нашего сайта.</Form.Check.Label>
                                        </Form.Check>
                                    </Form.Group>

                                    <Button variant="primary" type="submit">
                                        Submit
                                    </Button>
                                    </Form>
                                    )}
                                />
                            </Col>
                        </Row>
                    </Container>
                </Fragment>
            )
        }

    }
}

Register = observer(Register);

export default withCookies(withRouter(Register));