import React, { Fragment } from 'react';
import { Container, Row, Col } from 'react-bootstrap';

class About extends React.Component {

    render() {
        return (
            <Fragment>
                <Container fluid className="about-container">
                    <Row>
                        <Container className="about-container-inside">
                            <Row>
                                <Col xs={12}>
                                    <h1>Будьте всегда на связи с нами</h1>
                                    <p>Вы всегда можете связаться с нами в соц. сетях. Пишите свои предложения по поводу нашей работы нам в VK. Вы также можете связаться с нами по любому возникнувшему вопросу. Мы стараемся ответить каждому.</p>
                                </Col>
                                <Col xs={12} className="social-icons">
                                    <a><i class="fab fa-vk"></i></a>
                                    <a><i class="fab fa-telegram-plane"></i></a>
                                    <a><i class="fab fa-facebook-f"></i></a>
                                    <a><i class="fab fa-whatsapp"></i></a>
                                </Col>
                            </Row>
                        </Container>
                    </Row>
                </Container>    
            </Fragment>
        )
    }
}

export default About;