import React, { Fragment } from 'react';
import { Container, Row, Col, Form, FormGroup, FormControl, ControlLabel, Carousel, Badge, Button, Image } from 'react-bootstrap';

class Mce extends React.Component {

    render() {
        return (
            <Fragment>
                    <Container className="main-features-block">
                        <Row>
                            <Col xs={12} className="main-features"><h1>Почему выбирают нас</h1></Col>
                            <Col className="main-features-group" xs={12}>
                                <div className="features-item">
                                    <div className="icon-circle">
                                        <span>
                                            <i class="far fa-thumbs-up"></i>
                                        </span>
                                    </div>
                                    <h3>Качественно</h3>
                                    <p>Опытные,образованные сотрудники залог хорошего бизнеса</p>     
                                </div>
                                <div className="features-item">
                                    <div className="icon-circle">
                                        <span>
                                            <i class="far fa-building"></i>
                                        </span>
                                    </div>    
                                    <h3>Удобно</h3>
                                    <p>Коплексное обслуживание домов и сооружений</p>     
                                </div>
                                <div className="features-item">
                                    <div className="icon-circle">
                                        <span><i class="fas fa-hand-holding-usd"></i></span>
                                    </div>    
                                    <h3>Доступно</h3>
                                    <p>Демократичные цены без лишних надбавок</p>     
                                </div>                           
                            </Col>
                        </Row>    
                    </Container>
            </Fragment>
        )
    }
}

export default Mce;