import React, { Fragment } from 'react';
import { withRouter } from 'react-router-dom';
// import { observer } from 'mobx-react';
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';
import Form from 'react-bootstrap/Form';
import { Row, Col, Button, Container } from 'react-bootstrap';
import './style.css';

class MeterMeasurements extends React.Component {
    // Страница показаний счетчика с кнопкой Оплатить (Открывается через меню ЖКХ)

    componentDidMount() {
    }

    onHandlePayment = (e) => {}

    render() {
        var payments = [{
            meter: "10200033",
            type: "Холодная вода",
            tarif: "Всего к оплате: 5458.56 руб.",
            start_measure: "53,840 куб. м.",
            final_measure: "56,840 куб. м.",
            expenses: "3,000 куб.м.",
            sum: "90,5 p"
        }, {
            meter: "25000007",
            type: "Холодная вода",
            tarif: "Всего к оплате: 5458.56 руб.",
            start_measure: "53,840 куб. м.",
            final_measure: "56,840 куб. м.",
            expenses: "3,000 куб.м.",
            sum: "290,37 p"
        }, {
            meter: "33300055",
            type: "Электроэнергия",
            tarif: "День (10р/кВтМ)",
            start_measure: "53,840 куб. м.",
            final_measure: "56,840 куб. м.",
            expenses: "",
            sum: "147,30 p"
        }, {
            meter: "33300055",
            type: "Электроэнергия",
            tarif: "Ночь (5р/кВтМ)",
            start_measure: "53,840 куб. м.",
            final_measure: "56,840 куб. м.",
            expenses: "",
            sum: ""
        }];
        return (
            <Fragment>
                <Container fluid className="mce-container">
                <h1>Расчитать оплату</h1>
                <Form>
                    <Form.Row>
                        <Form.Group as={Col} xs={5} md={6}>
                            <Form.Label>Начальная дата:</Form.Label>
                            <Form.Control placeholder="01.05.2019" />
                        </Form.Group>
                        <Form.Group as={Col} xs={5} md={6}>
                            <Form.Label>Конечная дата:</Form.Label>
                            <Form.Control placeholder="18.05.2019" />
                        </Form.Group>
                    </Form.Row>
                </Form>
                <BootstrapTable striped data={payments} hover>
                    <TableHeaderColumn isKey dataField='meter'>Счетчик</TableHeaderColumn>
                    <TableHeaderColumn dataField='type'>Тип</TableHeaderColumn>
                    <TableHeaderColumn dataField='tarif'>Тариф</TableHeaderColumn>
                    <TableHeaderColumn dataField='start_measure'>Начальные показания</TableHeaderColumn>
                    <TableHeaderColumn dataField='final_measure'>Конечные показания</TableHeaderColumn>
                    <TableHeaderColumn dataField='expenses'>Расход за период</TableHeaderColumn>
                    <TableHeaderColumn dataField='sum'>$</TableHeaderColumn>
                </BootstrapTable>
                <div>
                    <p>Итого по счетчикам: 2458.56</p>
                    <span>Прочие КУ: 3000</span>
                    <span>Всего к оплате: 5458.56 руб.</span>
                </div>
                <Button variant="primary" type="submit" onClick={() => this.props.history.push('/payment')}>
                    Оплатить
                </Button>
                </Container>
            </Fragment>
        )
    }
}

export default withRouter(MeterMeasurements);