import React, { Component } from 'react'
import { Redirect } from 'react-router-dom';
import { Col, Row, Container, Table, Spinner, Button } from 'react-bootstrap';
import { withCookies } from 'react-cookie';
import './style.css';

class index extends Component {

    constructor(props) {
        super(props);
        
        this.state = { redirect: false };
    }

    componentDidMount() {
        this.props.store.checkSession(this.props.cookies.get('auth'))
        this.setState({ redirect: this.isAuthenticated });
    }

    render() {

        const { isAuthenticated } = this.props.store;

        if(this.state.redirect === true) {
            return <Redirect to={{
                pathname: '/',
                state: { from: this.props.location }
            }}/>
        }

        return isAuthenticated === true ? (
            <div className="admin-container">
                You are in admin panel
            </div>
        ) : <div>Loading...</div>
    }
}

export default withCookies(index);
