import React, { Fragment } from 'react';
import { Container, Row, Col } from 'react-bootstrap';

class feedback extends React.Component {

    render() {
        return (
            <Fragment>
                <Container fluid className="feedback-container">
                    <Row>
                        <Container>
                            <Row><Col xs={12} md={6} className="feedback-text-col"><div className="label-30"><p>Обратная связь.</p></div><p>Исправляем недочёты.
Когда мы получаем от вас плохие отзывы, мы спрашиваем у вас, что не так и пытаемся всячески исправить проблему и улучшить качество работы. Мы стараемся для вас, мы хотим чтобы ваша жизнь стала лучше.</p></Col>
                    <Col xs={12} md={6} className="feedback-text-col"><div className="label-30"><p>Исправляем недочёты.</p></div><p>Вы можете написать нам свои жалобы и предложения по улучшению нашей работы, мы всегда рады вас выслушать. Мы обязательно ответим каждому. Также оставьте нам свой отзыв, так мы поймём устраивает ли вас качество нашей работы. Получать от вас хорошие отзывы нам очень приятно, спасибо.</p></Col></Row>
                        </Container>
                    </Row>  
                </Container>    
            </Fragment>
        )
    }
}

export default feedback;