import React, { Fragment } from 'react';
// import { observer } from 'mobx-react';
import { ListGroup, Col, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Form from 'react-bootstrap/Form';

class Payment extends React.Component {
    // Страница формы оплаты ЖКХ

    componentDidMount() {
    }

    render() {
        return (
            <Fragment>
                <Form>
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Номер карты</Form.Label>
                        <Form.Control name="login" type="text" placeholder="XXXX XXXX XXXX XXXX" />
                    </Form.Group>
                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Имя Фамилия</Form.Label>
                        <Form.Control name="password" type="password" placeholder="Password" />
                    </Form.Group>
                    <Form.Row>
                        <Form.Group as={Col} xs={5} md={6}>
                            <Form.Label>Срок действия</Form.Label>
                            <Form.Control name="password" type="number" placeholder="05" md={1} />
                            <Form.Control name="password" type="number" placeholder="2019" md={2} />
                        </Form.Group>
                        <Form.Group as={Col} xs={2} md={4}>
                            <Form.Label>CVV2/CVC2:</Form.Label>
                            <Form.Control placeholder="321" />
                        </Form.Group>
                    </Form.Row>
                    <Form.Row>
                        <Button variant="primary" type="submit" onClick={this.onHandleLogin}>
                            Оплатить
                        </Button>
                    </Form.Row>
                </Form>
            </Fragment>
        )
    }
}

export default Payment;