import React, { Fragment } from 'react';
import { observer } from 'mobx-react';
import { Redirect } from 'react-router-dom';
import './style.scss';  
import { Container, Row, Col, Form, FormGroup, FormControl, ControlLabel, Carousel, Badge, Button } from 'react-bootstrap';
import Features from './features';
import About from './about';
import Price from './price';
import FeedBack from './feedback';
import Mce from './mce'

class MainPage extends React.Component {
    // Главная страница

    constructor(props) {
        super(props);
        this.state = { 
            navigate: false
         }
    }

    componentDidMount() {
        const userId = 1;
        // this.props.store.getProfile(userId);
    }

    redirectTo() {
        this.setState({ navigate: true });
    }

    render() {
        const { citizen, isLoading, isAuthenticated } = this.props.store;

        if(this.state.navigate && this.props.store.isAuthenticated) {
            return <Redirect to='/profile' />;
        }

        if(this.state.navigate && !this.props.store.isAuthenticated) {
            return <Redirect to='/login' />;
        }

        return (
            <Fragment>
                <Container fluid className="container-wrapper">
                    <Row>
                    <Container fluid className="main-container">
                        <Row>
                            <Container>
                                <Row>
                                    <div className="main-banner">
                                        <h1>Лучший сервис по обслуживанию жилых домов и комплексов</h1>
                                        <button className="btn-s btn-action btn_lg btn-green" onClick={() => this.redirectTo()}>Попробовать</button>
                                    </div>
                                </Row>
                            </Container>
                        </Row>
                    </Container>
                    <Mce />
                    <About />
                    <FeedBack />
                    </Row>
                </Container>
            </Fragment>
        )
    }
}

export default MainPage;