import React, { Fragment } from 'react';
import { withRouter, Redirect } from 'react-router-dom';
import { observer } from 'mobx-react';
import { Row, Col, Button, Container } from 'react-bootstrap';
import { withCookies } from 'react-cookie';
import Form from 'react-bootstrap/Form';
import './style.scss';

class Login extends React.Component {
    // Tutorial: https://medium.freecodecamp.org/login-using-react-redux-redux-saga-86b26c8180e

    state = {
        redirect: false,
        email: '',
        password: ''
    };


    componentDidMount() {
    }

    async componentWillReact() {
        if(this.props.store.isAuthenticated) {
            // await this.props.store.fetchProfile()
            this.props.history.push('/')
        }
    }

    changeValue(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    onHandleLogin = (event) => {
        event.preventDefault();
        if(this.state.email === 'admin' && this.state.password === 'admin') {
            this.props.cookies.set('auth', true);
            this.props.store.login();
            this.setState({ redirect: true });
        }
      //  this.props.store.login({ email: this.state.email, password: this.state.password });
    }

    render() {
        const { isAuthenticated } = this.props.store;

        if(this.state.redirect) { //isAuthenticated) {
            return <Redirect to="/login" />;
        }
        else {
            return (
                <Fragment>
                    <Container className="login-container">
                        <Row>
                            <Col className="login-block">
                                <h1>ВХОД</h1>
                                <Form>
                                    <Form.Group controlId="formBasicEmail">
                                        <Form.Label>E-mail</Form.Label>
                                        <Form.Control name="email" type="email" onChange={(e) => this.changeValue(e)} value={this.state.email} placeholder="Введите E-mail" />
                                    </Form.Group>
                                    <Form.Group controlId="formBasicPassword">
                                        <Form.Label>Пароль</Form.Label>
                                        <Form.Control name="password" type="password" onChange={(e) => this.changeValue(e)} value={this.state.password} placeholder="Введите пароль" />
                                    </Form.Group>
                                    <button className="btn-s btn_md btn-action btn-green" variant="primary" type="submit" onClick={this.onHandleLogin}>
                                        Войти
                                    </button>
                                </Form>
                            </Col>
                        </Row>
                    </Container>
                </Fragment>
            )
        }

    }
}

Login = observer(Login);

export default withCookies(withRouter(Login));