import { observable, action, decorate } from 'mobx';
import * as CitizenAPI from '../CitizenAPI';

class CitizenStore {
    isAuthenticated = false;
    citizen = {};
    isLoading = false;

    login = async (params) => {
         /* await CitizenAPI.login(params).then(data => {
             console.log(data);
         }); */ 

         this.isAuthenticated = true;
    }

    logout = () => {
        this.isAuthenticated = false;
    }

    checkSession = async (token) => {
        /* CitizenAPI.checkSession(token).then(data => {
            if(data) this.isAuthenticated = true;
        }) */
        token === 'true' ? this.isAuthenticated = true : void 0;
    }

    getProfile = async (userId) => {
        console.log("getProfile");
        this.isLoading = true;
        CitizenAPI.getProfile(userId);
        await CitizenAPI.getProfile(userId).then(data => {
            this.isLoading = false;
            this.citizen = data
        });
    }

}

decorate(CitizenStore, {
    isAuthenticated: observable,
    login: action,
    getProfile: action,
    checkSession: action
})

export default CitizenStore;