import React, { Fragment } from 'react';
import './style.css';
import { Link } from 'react-router-dom';
import { Row, Col } from 'react-bootstrap';
import { Container } from 'semantic-ui-react';

class Footer extends React.Component {
    // футер

    render() {
        return (
            <Fragment>
                        <div className="footer-container">
                                <Container className="mx-w-100">
                                    <Row className="footer-grid-top">
                                        <Col className="footer-company" md={6} xs={12}><h2>Hyppnotic</h2>
                                            <span>
                                            example@gmail.com
                                            </span>
                                        </Col>
                                        <Col className="footer-links" lg={3} md={3} xs={6}><h4>О компании</h4>
                                            <div className="footer-links-items">
                                                <Link to="#">Адрес</Link>
                                                <Link to="#">Github</Link>
                                                <Link to="#">Facebook group</Link>
                                                <Link to="#">Связь по E-Mail</Link>
                                            </div>
                                        </Col>
                                        <Col className="footer-links" lg={3} md={3} xs={6}><h4>Ссылки</h4>
                                            <div className="footer-links-items">
                                                <Link to="#">ЖКХ</Link>
                                                <Link to="#">Объявления</Link>
                                                <Link to="#">Голосование</Link>
                                                <Link to="#">Контакты</Link>
                                            </div>
                                        </Col>
                                    </Row>
                                </Container>
                            <Row className="footer-grid-bottom">
                                    <Col>
                                        <p>Все права защищены @2019</p>
                                    </Col>
                            </Row>
                        </div>
            </Fragment>           
        )
    }
}

export default Footer;