const formate = (method, params) => {
    return {"id": 1, "jsonrpc": "2.0", "method": method, "params": params};
}

export const checkSession = (token) => {
    let payload = {
        method: 'POST',
        mode: 'cors',
        body: JSON.stringify(formate('checkSession', token))
    }

    return fetch(api, payload)
    .then(res => res.text())
    .then((data) => data)
}

export const login = (params) => {
    let payload = {
        method: 'POST',
        mode: 'cors',
        // headers: this.buildHeaders(token),
        body: JSON.stringify(formate('login', params))
    }
    console.log(JSON.stringify(formate('login', params)));
    return fetch(api, payload)
    .then(res => res.text())
    .then((data) => data
    );

    // const res = await this.apiCall(api.login, 'POST', false, params);
    //     this.handleCommonError(res);
    //     return res.body;
}

