import React from 'react';
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import './App.css';
import CitizenStore from './stores/CitizenStore';
import MeterStore from './stores/MeterStore';
import MeterMeasurements from './components/Blocks/MeterMeasurements/index';
import Payment from './components/Blocks/Payment/index';
import Login from './components/Blocks/Login/index';
import Header from './components/Layouts/Header/index';
import Footer from './components/Layouts/Footer/index';
import Profile from './components/Blocks/Profile/index';
import MainPage from './components/Blocks/MainPage/index';
import { Container, Row, Col } from 'react-bootstrap';
import 'semantic-ui-css/semantic.min.css'
import Register from './components/Blocks/Register/index';
import Votes from './components/Blocks/Votes/index';
import Admin from './components/Admin/index';
import { withCookies } from 'react-cookie';

const store = new CitizenStore();
const meterStore = new MeterStore();

class App extends React.Component {

  componentWillMount() {
    store.checkSession(this.props.cookies.get('auth'));
  }

  render() {
    
    const PrivateRoute = ({ component: Component, ...rest }) => (
      <Route {...rest} render={(props) => (
        store.isAuthenticated === true
          ? <Component {...props} />
          : <Redirect to='/login' />
      )} />
    )

    return (
      <Router>
            <Header store={store} />
            <Switch>
              <Route path="/login" component={() => <Login store={store} />} />
              <Route path="/register" component={() => <Register store={store} />} />
              <Route exact path="/" component={() => <MainPage store={store} />} />
              <PrivateRoute path="/profile" component={() => <Profile store={store} />} />
              <PrivateRoute path="/ad" component={() => <Profile store={store} />} />
              <PrivateRoute path="/votes" component={() => <Votes store={store} />} />
              <PrivateRoute path="/mce" component={() => <MeterMeasurements store={meterStore} />} />
              <PrivateRoute path="/payment" component={() => <Payment />} />
              <PrivateRoute path="/admin" component={() => <Admin store={store} />} />
            </Switch>
            <Footer />
      </Router>
    );
  }
}

export default withCookies(App);
